<template>
  <flow-network-navigation-ui
    :search="search"
    :currentUrl="currentUrl"
    :routeLinks="routeLinks"
    :isFetchingData="isFetchingData"
    :fetchFlowNetworks="fetchFlowNetworkClones"
  ></flow-network-navigation-ui>
</template>
<script>
import FlowNetworkNavigationUi from '../FlowNetwork/flowNetworkNavigationUi.vue';
import { rfFlowNetworkCloneService } from '../../../services/rfFlowNetworkCloneService';
import { ROUTE_MAP } from '../../../uiservice/flowNetwork';
export default {
  name: 'flow-network-clone-navigation',
  data: () => ({
    search: '',
    routeLinks: [],
    currentUrl: '',
    flowNetworks: [],
    isFetchingData: false,
  }),
  components: { FlowNetworkNavigationUi },
  mounted() {
    this.fetchFlowNetworkClones();
  },
  watch: {
    '$route.path': {
      handler: function (path) {
        this.currentUrl = path;
      },
      deep: true,
      immediate: true,
    },
  },
  methods: {
    fetchFlowNetworkClones() {
      this.isFetchingData = true;
      rfFlowNetworkCloneService
        .fetch({ with_consumers: true, with_stream_clones: true })
        .then(data => {
          this.flowNetworks = data;
          this.constructRouteLinks();
        })
        .finally(() => {
          this.isFetchingData = false;
        });
    },
    routeLink(id, name, prefix) {
      return {
        id: id,
        name: `${prefix} ${name}`,
      };
    },
    flowNetworkLink() {
      return ROUTE_MAP.flowNetworkClone();
    },
    flowNetworkStreamsLink(flowNetworkId) {
      return ROUTE_MAP.streamClones(flowNetworkId);
    },
    consumerLink(flowNetworkId, streamId) {
      return ROUTE_MAP.consumers(flowNetworkId, streamId);
    },
    writersLink(flowNetworkId, streamId, producerId) {
      return ROUTE_MAP.writers(flowNetworkId, streamId, producerId);
    },
    constructRouteLinks() {
      this.routeLinks = [
        {
          id: 1,
          name: 'All Flow Networks',
          type: 'network',
          link: this.flowNetworkLink(),
          children: this.getTreeViewData(this.flowNetworks),
          count: this.flowNetworks.length,
        },
      ];
    },
    getTreeViewData(flowNetworks) {
      return flowNetworks.map(flowNetwork => ({
        ...this.routeLink(flowNetwork.uuid, flowNetwork.name, 'Flow N/W:'),
        link: this.flowNetworkStreamsLink(flowNetwork.uuid),
        type: 'flowNetworks',
        count: flowNetwork.stream_clones && flowNetwork.stream_clones.length,
        children:
          flowNetwork.stream_clones &&
          flowNetwork.stream_clones.map(stream => ({
            ...this.routeLink(stream.uuid, stream.name, 'Stream Clone:'),
            type: 'writer',
            link: this.consumerLink(flowNetwork.uuid, stream.uuid),
            count: stream.consumers && stream.consumers.length,
            children:
              stream.consumers &&
              stream.consumers.map(consumer => ({
                ...this.routeLink(consumer.uuid, consumer.name, 'Consumer:'),
                type: 'writerClones',
                count: consumer.writer_clones && consumer.writer_clones.length,
                link: this.writersLink(flowNetwork.uuid, stream.uuid, consumer.uuid),
              })),
          })),
      }));
    },
  },
};
</script>
<style>
.network-nav .text-style {
  text-decoration: unset;
  color: unset;
  font-size: 0.8125rem;
  line-height: 1rem;
}

.full-height {
  height: 100%;
}

.active-item {
  font-weight: bold !important;
  font-size: 1rem !important;
}

.navigation-item-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navigation-wrapper {
  border-right: 1px solid #333;
  padding: 0.5em;
}
</style>


